.button {
    height: 30px;
    width: 133px;
    color: 	#FFFFFF;
    border-radius: 3px;
}

.disabled {
    background-color: #004D99;
    border: 1px solid #004D99;
}

.secondary {
    color: #0066CC;
}

.default {
    background-color: #004D99;
}

.red {
    background-color: #CC1439;
}

.default:hover {
    background-color: #0080FF;
}

.red:hover {
    background-color: #FF1A47;
}

.secondary:hover {
    background-color: #E5F0FA;
}


.default:active {
    background-color: #0059B2;
}

.secondary:active {
    background-color: #C9E5FF;
}

.red:active {
    background-color: #B21232;
}

.button:disabled {
    opacity: 50%;
}
.buttonSave{
    background-color: #0066CC;
    color: white;
    margin-right: 30px;
}
.buttonRestore{
    background-color: white;
    color: #0066CC;
    border: 1px solid #0066CC;
}